import { request } from "../../request/request.js"
export default {
	data() {
		return {
			swiperList:[]
		}
	},
	onLoad() {
		let that = this;
		request({
		   url: '/getSwiperPic',
		   method: 'GET',
		  }).then(res => {
				if(res.data.status === '200'){
					console.log(res.data)
					that.swiperList = res.data.data;
				}
		});
	},
	methods: {
		
	}
}
